<template>
  <div id="hello">
    helloworld, 第{{index + 1}}位用户：{{username}}
  </div>
</template>

<script>
    export default {
        name: 'HelloWorld',
        data() {
            return {
                index: -1,
                username: ''
            }
        },
        mounted() {
            this.index = this.$route.query.index;
            this.username = this.$route.query.username;
        }
    }
</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }
</style>



