<template>
  <div class="users">
    <ul>
      <li v-for="user, index in users" :key="index" @click="toHello(index, user.name)">
        <h2>{{user.name}}</h2>
        <h2>{{user.email}}</h2>
      </li>
    </ul>
  </div>
</template>

<script>
    export default {
        name: "Users",
        data() {
            return {}
        },
        props: {
            users: {
                type: Array
            }
        },
        components: {},
        methods: {
            toHello: function (index, name) {
                // this.$router.push("/helloworld")
                this.$router.push({
                    path: "/helloworld",
                    query: {
                        index: index,
                        username: name
                    }
                })
            }

        },
        computed: {},
        watch: {},
    }

</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }

  .users {
    width: 100%;
    max-width: 1200px;
    margin: 0 auto;
    box-sizing: border-box;
  }

  ul {
    display: flex;
    flex-wrap: wrap;
    list-style: none;
  }

  li {
    border: 1px solid black;
    flex-grow: 1;
    flex-basis: 200px;
    text-align: center;
    padding: 20px;
    margin: 10px;
  }

</style>



