<template>
  <div class="home">
    <Header></Header>
    <Users :users="users"></Users>
    <Footer></Footer>
  </div>
</template>

<script>
    import Header from "./Header";
    import Footer from "./Footer";
    import Users from "./Users";

    export default {
        name: "Home",
        data() {
            return {
                users: []
            }
        },
        props: {},
        components: {Header, Users, Footer},
        methods: {},
        computed: {},
        watch: {},
        created() {
            this.$http.get('http://jsonplaceholder.typicode.com/users').then((data) => {
                this.users = data.body;
            })
        }
    }

</script>

<style scoped>
  * {
    margin: 0;
    padding: 0;
  }
</style>



